
import { IonImg, IonContent, IonHeader, IonPage, IonTitle, IonToolbar,modalController  } from '@ionic/vue';
import { defineComponent } from 'vue';
import Modal from './Facility.vue';

export default defineComponent({
  name: 'Home',
  components: {
    IonImg,
    IonContent,
    IonPage,
  },
  methods: {
    async openModal() {
      const modal = await modalController
        .create({
          component: Modal,
          cssClass: 'my-custom-class',
          componentProps: {
            title: 'New Title'
          },
        })
      return modal.present();
    },
  },
});
