
import { IonImg, IonContent, IonHeader, IonPage, IonTitle, IonToolbar,modalController } from '@ionic/vue';
import { defineComponent } from 'vue';
import Modal from './LoginUser.vue';
import { usePhotoGallery } from '@/composables/usePhotoGallery';

export default defineComponent({
  name: 'Home',
  components: {
    IonImg,
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar
  },
  setup(){
    const { takePhoto } = usePhotoGallery();
    return {
      takePhoto,
    }
  },
  methods: {
    async openModal() {
      await modalController.dismiss(); 
    },

  },
});
