<template>
  <ion-page>
    <ion-header :translucent="true">
      <ion-toolbar>
        <ion-buttons >
          <ion-button color="secondary">
            <ion-img src="/assets/img/jikoshokai.png" class="profile_img"></ion-img>
          </ion-button>
        </ion-buttons>
        <ion-title>ようこそたなかさん</ion-title>
        <ion-buttons slot="end" @click="openMenuModal">
          <ion-img src="/assets/img/menu.png" class="menu_img"></ion-img>
        </ion-buttons>
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true" id="background">
      <div id="container">
        <ion-slides pager="true" :options="slideOpts">
          <ion-slide :key="l" v-for="l in imgs">
            <ion-grid>
              <ion-row>
                <ion-col size="6"  :key="img" v-for="img in l">
                  <ion-button color="favorite" class="class_button" @click="openDetailModal(img.img_id,img.img_url,img.type)">
                    <ion-grid>
                  <ion-row> 
                    <ion-col v-if="img.type=='.mp4'">
                      <video ref="video" id="video" :src="img.img_url"  style="width:30vh; height:20vh;"  playinline></video>
                      <div class="tagbox">
                        <p style="font-size:30px; color:black">#{{img.array_tag_names}}</p>
                      </div>
                    </ion-col>
                    
                    <ion-col v-else>
                      <ion-img :src="img.img_url" class="class_img" style="width:30vh; height:20vh;"></ion-img> 
                      <div class="tagbox">
                        <p style="font-size:30px; color:black">{{img.array_tag_names}}</p>
                      </div>
                    </ion-col>
                  </ion-row>
                </ion-grid>
                  </ion-button>
                </ion-col>
              </ion-row>
            </ion-grid>
          </ion-slide>
        </ion-slides>
      </div>
      <ion-fab vertical="bottom" horizontal="end">
            <ion-fab-button @click="openCameraModal">
              <ion-img src="/assets/img/satuei.png" class="class_img"></ion-img>
            </ion-fab-button>
        </ion-fab>
    </ion-content>
  </ion-page>
</template>

<script lang="ts">
import { IonImg, IonContent, IonHeader, IonPage, IonTitle, IonToolbar, IonSlides, IonSlide,modalController } from '@ionic/vue';
import { defineComponent } from 'vue';
import { usePhotoGallery } from '@/composables/usePhotoGallery';
import { useVideoGallery } from '@/composables/useVideoGallery';
import { Storage } from '@capacitor/storage'
import { MediaCapture } from "@ionic-native/media-capture";
import { ref } from "vue";
import { Capacitor } from "@capacitor/core";
import { Filesystem, Directory } from "@capacitor/filesystem";
import MenuModal from './Menu.vue';
import CameraModal from './CameraMenu.vue';
import DetailModal from './Detail.vue';
import axios from 'axios';
//axios.defaults.baseURL = 'http://192.168.11.6:8100';
const url = "https://hu-fujilab.work/child_photo/get_photo_list.php";

export default defineComponent({
  name: 'Home',
  components: {
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar,
    IonSlides,
    IonSlide
    
  },
  setup(){
    const { photos, loadSaved } = usePhotoGallery();
    const PHOTO_STORAGE = "photos";
    const imgs = ref([]);
    const lists = async ()=>{
      const lists: any = [];
      await axios.get(url,{
        params:{
          "facility_id":1,
          "auth_kind":2,
          "class_id":1,
          "user_id":1
        },
      })
      .then(res => {
          const id = 0;
          while(id < res.data.length){
            lists.push(res.data.splice(id,id+4));
          }
          imgs.value = lists;
      }).catch(error => {
          new Error(error)
      });
      return lists
    }

    lists();


    const slideOpts = {
      initialSlide: 0,
      speed: 400
    };
    return {
      slideOpts,
      photos,
      loadSaved,
      imgs
    }
  },
  methods:{
    async openMenuModal() {
      const modal = await modalController
        .create({
          component: MenuModal,
          cssClass: 'my-custom-class',
          componentProps: {
            title: 'New Title'
          },
        })
      return modal.present();
    },
    async openCameraModal() {
      const modal = await modalController
        .create({
          component: CameraModal,
          cssClass: 'my-custom-class',
          componentProps: {
            title: 'New Title'
          },
        })
      return modal.present();
    },
    async openDetailModal(id: any,src: any,type: any) {
      const modal = await modalController
        .create({
          component: DetailModal,
          cssClass: 'my-custom-class2',
          componentProps: {
            title: 'New Title',
            imgid: id,
            imgsrc:src,
            type:type
          },
        })
      return modal.present();
    },
  },
});
</script>

