
import { IonIcon, IonContent, IonHeader, IonPage, IonTitle, IonToolbar } from '@ionic/vue';
import { defineComponent,ref,onMounted, watch  } from 'vue';
import { useVideoGallery } from '@/composables/useVideoGallery';



export default defineComponent({
  name: 'Video',
  components: {
    IonContent,
    IonPage,
    
  },
  setup(){
    const { initVideo, startVideo, stopVideo, playVideo, previewVideo } = useVideoGallery();
    initVideo();
    return {
      initVideo,
      startVideo,
      stopVideo,
      playVideo,
      previewVideo
    }
  }
});
