
import { IonImg, IonContent, IonHeader, IonPage, IonTitle, IonToolbar } from '@ionic/vue';
import { defineComponent } from 'vue';
import { usePhotoGallery } from '@/composables/usePhotoGallery';

export default defineComponent({
  name: 'Home',
  components: {
    IonImg,
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar
  },
  setup(){
    const { photos, loadSaved } = usePhotoGallery();

    return {
        photos,
        loadSaved
    }
  }
});
