import { ref, onMounted, watch  } from 'vue';
import { Camera, CameraResultType, CameraSource, Photo } from '@capacitor/camera';
import { Filesystem, Directory } from '@capacitor/filesystem'
import { Storage } from '@capacitor/storage'
import { modalController } from '@ionic/vue';
import axios from 'axios';

axios.defaults.baseURL = 'http://192.168.11.6:8100';



const url = "https://hu-fujilab.work/child_photo/file_upload.php";

export function usePhotoGallery() {
  const PHOTO_STORAGE = "photos";
  const photos = ref<Photo[]>([]);
  const imgs = ref([]);

  const cachePhotos = () => {
    Storage.set({
      key: PHOTO_STORAGE,
      value: JSON.stringify(photos.value)
    });
  }

  const loadSaved = async () => {
    const photoList = await Storage.get({ key: PHOTO_STORAGE });
    const photosInStorage = photoList.value ? JSON.parse(photoList.value) : [];
  
    for (const photo of photosInStorage) {
      const file = await Filesystem.readFile({
          path: photo.path,
          directory: Directory.Data
      });
      photo.webPath = `data:image/jpeg;base64,${file.data}`;
    }

  
    photos.value = photosInStorage;
  }

  const convertBlobToBase64 = (blob: Blob) => new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onerror = reject;
    reader.onload = () => {
        resolve(reader.result);
    };
    reader.readAsDataURL(blob);
  });

  const savePicture = async (photo: Photo, fileName: string): Promise<Photo> => {
    
  
    // Fetch the photo, read as a blob, then convert to base64 format
    const response = await fetch(photo.webPath!);
    const blob = await response.blob();
    const base64Data = await convertBlobToBase64(blob) as string;
  
    const savedFile = await Filesystem.writeFile({
      path: fileName,
      data: base64Data,
      directory: Directory.Data
    });

    //axios
    const data = new FormData();
    data.append('files[]', base64Data+'||'+'test'+'||'+'test'+'||'+'1'+'||'+'https://hu-fujilab.wrok/child_photo/DATA/0001_facility/image/0001_test/2_0001_test'+'||'+'1');
    
    axios.post(url,data,{
      headers:{'content-type': 'multipart/form-data'}

    })
    .then(res => {
        console.log(res)
        
        window.location.reload();
    }).catch(error => {
        new Error(error)
    });


    
    return {
      path: fileName,
      webPath: photo.webPath,
      format:'jpeg'
    };
  }

  const takePhoto = async () => {
    const cameraPhoto = await Camera.getPhoto({
      allowEditing: true,
      resultType: CameraResultType.Uri,
      source: CameraSource.Camera,
      webUseInput:true,
      quality: 100,
      saveToGallery:true
    });

    const fileName = new Date().getTime() + '.jpeg';
    const savedFileImage = await savePicture(cameraPhoto, fileName);
    photos.value = [savedFileImage, ...photos.value];

    
    await modalController.dismiss(); 
  };

    
  watch(photos, cachePhotos);
  onMounted(loadSaved);
  
    return {
      photos,
      takePhoto,
      loadSaved,
    };
}