
import { IonImg, IonContent, IonHeader, IonPage, IonTitle, IonToolbar,modalController } from '@ionic/vue';
import { defineComponent } from 'vue';
import Modal from './LoginUser.vue';
import { ref } from "vue";
import axios from 'axios';
const url = "https://hu-fujilab.work/child_photo/get_photo_list.php";

export default defineComponent({
  name: 'Detail',
  components: {
    IonImg,
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar
  },
  props: {
    title: { type: String, default: 'Super Modal' },
    imgid: { type: String, default: '0' },
    imgsrc: { type: String, default: '' },
    type: { type: String, default: '.png' },
  },
  setup(props){
    //データを取得
  },
  methods: {
    async openModal() {
      await modalController.dismiss(); 
      const modal = await modalController
        .create({
          component: Modal,
          cssClass: 'my-custom-class',
          componentProps: {
            title: 'New Title'
          },
        })
      return modal.present();
    },
  },
});
