
import { IonImg, IonContent, IonHeader, IonPage, IonTitle, IonToolbar, modalController } from '@ionic/vue';
import { defineComponent } from 'vue';
import Modal from './LoginPass.vue';

export default defineComponent({
  name: 'Home',
  components: {
    IonImg,
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar
  },
  methods: {
    async openModal() {
      await modalController.dismiss();  
      const modal = await modalController
        .create({
          component: Modal, 
          cssClass: 'my-custom-class',
          componentProps: {
            title: 'New Title'
          },
        })
      return modal.present();
    },
  },
});
